<template>
  <div class="julian-login">
    <v-card class="pa-4">
      <v-form
        ref="form"
        v-model="valid"
      >
        <v-text-field
          v-model="uname"
          :rules="unameRules"
          label="Username"
          prepend-icon="person"
        />
        <v-text-field
          v-model="password"
          :append-icon="show ? 'visibility' : 'visibility_off'"
          :rules="pwdRules"
          :type="show ? 'text' : 'password'"
          label="Password"
          counter
          @click:append="show = !show"
          prepend-icon="vpn_key"
        />
        <v-checkbox class="ml-4 pl-1" label="Remember Me"/>
        <v-btn
          color="info"
          class="right mr-0"
          :disabled="!valid"
          @click="validate"
        >
          Login
        </v-btn>
        <div class="mb-4 pb-2"></div>
      </v-form>
    </v-card>  
  </div>
</template>

<script>
export default {
  data: () => ({
    valid: true,
    uname: '',
    unameRules: [
      v => !!v || 'Userame is required'
    ],
    password: '',
    show: false,
    pwdRules: [
      v => !!v || 'Password is required',
      v => v.length >= 8 || 'Password Min 8 characters'
    ]
  }),
  methods: {
    validate () {
      if (this.$refs.form.validate()) {
        this.$router.push("/admin/dashboard")
      }
    }
  }
}
</script>

