<script>
import {Doughnut} from 'vue-chartjs'

export default {
  extends: Doughnut,
  data() {
    return {
      data: {
        labels: ['Full Stack', 'Front End', 'Back End', 'UI/UX', 'SQA', 'SRA'],
        datasets: [{
          data: [15,20,15,30,10,10],
          backgroundColor: ['#00B8CA', '#19C25D', '#FF9009', '#F70039', '#56ADF6', '#5D7B8A']
        }],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        title: {
          display: true,
          text: 'Employee Percentage'
        },
        legend: {
          position: 'right'
        }
      }
    }
  },
  mounted () {
    this.renderChart(this.data, this.options)
  }
}
</script>